.menu {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__options {
        display: grid;
        grid-template: auto / 1fr 1fr 1fr 1fr;
        @include s7 {
            grid-template: auto / 1fr 1fr;
        }
        @include s4 {
            grid-template: auto / 1fr;
        }
        gap: 1em;
        margin: 0 0 1em 0;

        border-radius: 15px;
        box-shadow: 0 7px 20px rgba(0, 0, 0, 0.07);
        background: var(--light);
        padding: 1em;
    }

    &__form {
        display: flex;
    }

    &__buttons {
        display: flex;
    }

    &__create {
        padding: 0.6em 1em;
        margin-right: 1em;
    }

    &__input {
        padding: 0.6em 1em;
        cursor: pointer;
        border-radius: 15px 0 0 15px ;
        border: 2px solid var(--primary_active);
        border-right: none;
        font-size: 14px;
        color: var(--grey);
        display: flex;
        align-items: center;
        background: var(--light);
        
        &:hover {
            background: var(--light_hover);
        }

        &:active {
            background: var(--light);
        }

        &::before {
            content: '';
            display: inline-block;
            width: 1em;
            height: 1em;
            background: url('../../img/arrow-up.svg') no-repeat;
            background-size: contain;
        }
    }

    &__submit {
        border-radius: 0 15px 15px 0;
    }
}